}
.skills ul > li em {
  font-size: 18px;
  position: relative;
  top: -30px;
}
.skills ul > li span {
  height: inherit;
  background-color: #000;
  background: linear-gradient(90deg, #000, red);
  position: absolute;
  left: 0;
}
